@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    .navbar-icon{
        @apply relative flex items-center justify-center h-12 w-12 mx-auto shadow-lg bg-gray-800 text-green-500 hover:bg-green-600;

    }

    .btn {
        @apply font-bold py-2 px-4 rounded;
      }
      .btn-blue {
        @apply bg-blue-500 text-white;
      }
      .btn-blue:hover {
        @apply bg-blue-700;
      }

}